<template>
  <div>
    <router-link
      to="/"
      tag="div"
      class="header-abs"
      v-show="showAbs"
    >
      <div class="iconfont header-abs-back">&#xe624;</div>
    </router-link>
    <div
      class="header-fixed"
      v-show="!showAbs"
      :style="opacityStyle"
    >
      <router-link to="/">
        <div class="header-fixed-back">
          <div class="iconfont back-fixed-icon">&#xe624;</div>
        </div>
      </router-link>
      灵山大佛
    </div>
  </div>
</template>

<script>
export default {
  name: 'DetailHeader',
  data () {
    return {
      showAbs: true,
      opacityStyle: {
        opacity: 0
      }
    }
  },
  methods: {
    handleScroll () {
      const top = document.documentElement.scrollTop
      if (top > 0) {
        let opacity = top / 206
        this.opacityStyle = { opacity: (opacity > 1 ? 1 : opacity) }
        this.showAbs = false
      } else {
        this.showAbs = true
      }
    }
  },
  activated () {
    window.addEventListener('scroll', this.handleScroll)
  },
  deactivated () {
    window.removeEventListener('scroll', this.handleScroll)
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/varibles.styl'
  .header-abs
    position: absolute
    top: .1rem
    left: .1rem
    width: .64rem
    height: .64rem
    line-height: .64rem
    border-radius: 50%
    background: rgba(0, 0, 0, .8)
    .header-abs-back
      text-align: center
      color: #fff
      font-size: .4rem
  .header-fixed
    position: fixed
    top: 0
    left: 0
    right: 0
    overflow: hidden
    height: $headerHeight
    background: $bgColor
    font-size: .32rem
    text-align: center
    color: #fff
    line-height: $headerHeight
    .header-fixed-back
      position: absolute
      top: 0
      left: 0
      width: .64rem
      font-size: .64rem
      .back-fixed-icon
        text-align: center
        font-size: .4rem
        color: #fff
</style>



