<template>
  <div>
    <div class="banner"  @click="handleGallaryShow">
      <img class="banner-img" :src="this.bimg">
      <div class="banner-info">
        <div class="banner-title">{{this.name}}</div>
        <div class="banner-number">
          <span class="iconfont banner-icon">&#xe616;</span>
            23
        </div>
      </div>
    </div>
    <fade-animation>
      <common-gallary
        :imgs="gimg"
        v-show="showGallary"
        @close="handleGallaryClose"
      >
      </common-gallary>
    </fade-animation>
  </div>
</template>

<script>
import CommonGallary from 'common/gallary/Gallary.vue'
import FadeAnimation from 'common/fade/fade.vue'
export default {
  name: 'DetailBanner',
  props: {
    name: String,
    bimg: String,
    gimg: Array
  },
  data () {
    return {
      showGallary: false
    }
  },
  components: {
    CommonGallary,
    FadeAnimation
  },
  methods: {
    handleGallaryShow () {
      this.showGallary = true
    },
    handleGallaryClose () {
      this.showGallary = false
    }
  }
}
</script>

<style lang="stylus" scoped>
  .banner
    position: relative
    overflow: hidden
    height: 0
    padding-bottom: 55%
    .banner-img
      width: 100%
    .banner-info
      display: flex
      position: absolute
      left: 0
      right: 0
      bottom: 0
      line-height: .6rem
      color: #fff
      background-image: linear-gradient(top, rgba(0, 0, 0, 0), rgba(0, 0, 0, .8))
      .banner-title
        flex: 1
        font-size: .36rem
        padding: .2rem
      .banner-number
        padding: 0 .4rem
        height: .4rem
        line-height: .4rem
        margin-top: .2rem
        border-radius: .2rem
        background: rgba(0, 0, 0, .8)
        font-size: .24rem
        .banner-icon
          font-size: .24rem
</style>



